import React, { Component, PropTypes } from 'react';
import cx from 'classnames';
import s from './Navigation.scss';
import withStyles from '../../decorators/withStyles';
import Link from '../Link';

@withStyles(s)
class Navigation extends Component {

  static propTypes = {
    className: PropTypes.string,
  };

  render() {
    return (
      <div className={cx(s.root, this.props.className)} role="navigation">
        <Link className={cx(s.link,{'menu-on': this.props.menu == 'inicio'})} to="/inicio" to="/inicio"><span className={s.circle}></span> Nosotros</Link>
        <Link className={cx(s.link,{'menu-on': this.props.menu == 'servicios'})} to="/servicios" ><span className={s.circle}></span> Servicios</Link>

        <Link className={cx(s.link,{'menu-on': this.props.menu == 'contacto'})}  to="/contacto"><span className={s.circle}></span> Contacto</Link>
        <Link className={s.link} to="/clientes"><span className={s.circle}></span> Clientes</Link>
        <Link className={s.link} to="/">Diloo app</Link>

      </div>
    );
  }

}

export default Navigation;



