import React, { Component } from 'react';
import s from './Header.scss';
import withStyles from '../../decorators/withStyles';
import Link from '../Link';
import cx from "classnames";
import Navigation from '../Navigation';

@withStyles(s)
class Header extends Component {
  constructor(props) {
   super(props);
   this.state = {open: 0}
 }

  render(){
    var cl = cx(s.root);
    var minilogo = cx(s.lefy);
    var logo = cx(s.logo,s.hide);
    if(this.props.open_menu == 1){
      cl = cx(s.root,s.open);
      minilogo = cx(s.lefy,s.hide)
      logo = cx(s.logo);
    }


    console.log(this.props);
    return (
      <div className={cl}>

          <button className={s.btn_menu} onClick={this.props.toggle.bind(this)}>
            <span className={s.ham}>
              <span></span>
              <span></span>
              <span></span>
            </span>
            <h4>Menu</h4>
          </button>
          <Navigation className={s.nav} menu={this.props.menu} />
          <Link className={s.brand} to="/">
            <img src={require('./micro-logo.png')} className={minilogo}  />
            <img src={require('./logo-small.png')} width="100"  className={logo} />

          </Link>


      </div>
    );
  }

}

export default Header;



