import React, { Component } from 'react';
import s from './Footer.scss';
import withStyles from '../../decorators/withStyles';
import Link from '../Link';

@withStyles(s)
class Footer extends Component {

  render() {
    return (
      <div className={s.root}>
        <div className={s.container}>
          <span className={s.text}><img src="/semana.png" /></span>

          <Link className={s.link} to="/"><img src="/utec.png" /></Link>

          <Link className={s.link} to="/"><img src="/alta.png" /></Link>

          <Link className={s.link} to="/"><img src="/yno.png" /></Link>

          <Link className={s.link} to="/"><img src="/kickstarter.png" /></Link>

          <Link className={s.link} to="/"><img src="/comercio.png" /></Link>

          <Link className={s.link} to="/"><img src="/start.png" /></Link>
  

        </div>
      </div>
    );
  }

}

export default Footer;



