import React, { Component, PropTypes } from 'react';
import s from './InitPage.scss';
import cx from 'classnames';
import Link from '../Link';
import withStyles from '../../decorators/withStyles';
import ReactDOM from 'react-dom';
import { scroller, scrollSpy,Element } from 'react-scroll';
import Header from '../Header';
import Location from '../../core/Location';
import parsePath from 'history/lib/parsePath';

cx.bind(s);

const title = 'DILOO APP';

@withStyles(s)
class InitPage extends Component {

  constructor(props) {
   super(props);
   this.state = {margin:0,no_menu: true,open_menu: 0, height: 600, on_servicio: 0, mouse_on: false, sms: false,modY:0, elements: ['inicio','servicio','contacto']}
 }

  static contextTypes = {
    onSetTitle: PropTypes.func.isRequired,
  };

  componentWillMount() {
    this.context.onSetTitle(title);
  }


  componentDidMount(){
    if (typeof document !== 'undefined') {
      this.time = 0;
      this.modY = 0;
      document.addEventListener('scroll', this.scrollHandler.bind(this));
      document.addEventListener('keydown', this._keyPress.bind(this));
    }
    this.setState({height: window.innerHeight});
    scroller.scrollTo(this.props.is, true, 0, 0);
  }
  handleResize(){

  }
  componentDidUpdate(){


    scroller.scrollTo(this.props.is, true, 0, 0);
  }

  scrollHandler(){
    window.clearInterval(this.time);
    var este = this;
    //  console.log(window.innerHeight + ' - ' + scrollSpy.currentPositionY())
    this.time = setInterval(function(){
      if(Math.abs(scrollSpy.currentPositionY() - este.state.modY) < 20){
            return false;
        }

      if(scrollSpy.currentPositionY() > (window.innerHeight*0.1) && scrollSpy.currentPositionY() < (window.innerHeight*1.1) ){
        Location.push({
          ...(parsePath('/servicios')),
          state: this.props && this.props.state || null,
        });

      } else if(scrollSpy.currentPositionY() > (window.innerHeight*1.1)){
        Location.push({
          ...(parsePath('/contacto')),
          state: this.props && this.props.state || null,
        });

      } else if(scrollSpy.currentPositionY() < (window.innerHeight/4)) {
        Location.push({
          ...(parsePath('/inicio')),
          state: this.props && this.props.state || null,
        });
      } else if((window.innerHeight) < scrollSpy.currentPositionY()) {
        este.handleResize()
      }
      este.setState({'modY':scrollSpy.currentPositionY()});
      window.clearInterval(this.time);
    },300);
  }

  componentWillUnmount() {
    scrollSpy.unmount();
    scroller.unmount();
    window.removeEventListener('resize', this.handleResize);
  }

  toggleMenu(event){
    console.log(scroller)
    if(this.state.open_menu == 0){
      this.setState({open_menu: 1})
    } else {
      this.setState({open_menu: 0})
    }

  }

  onServicio(servicio,e){
    console.log(e);
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    this.setState({mouse_on:true, on_servicio: servicio})
  }

  offServicio(e){
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();
    this.setState({mouse_on:false})
  }

  callSms(){
    this.setState({sms: true})
  }
  _keyPress(e){
    var este = this;

  }

  render() {

    var reg = cx(s.back_white);
    var is = false;
    var started = cx();
    if(this.props.as == 3){
      reg = cx(s.back_white,s.on)
      is = true;
    }
    var is_menu = cx(s.head);
    var arrow = cx();

    if(this.props.is != 'init'){
      is_menu = cx(s.menu_show,s.head);
      arrow = cx(s.hide);
      started = cx(s.show)

    }
    let on_this = s.on;
    return (

      <div className={s.root} onKeyPress={this._keyPress.bind(this)}>
        <div className={is_menu}>
          <Header is={is}  open_menu={this.state.open_menu} menu={this.props.is} toggle={this.toggleMenu.bind(this)}/>
        </div>
        <Link to="/inicio" className={arrow}>
          <svg className="arrows">
							<path className="a1" d="M0 0 L30 32 L60 0"></path>
							<path className="a2" d="M0 20 L30 52 L60 20"></path>
							<path clasName="a3" d="M0 40 L30 72 L60 40"></path>
				  </svg>
        </Link>

        <div className={s.wide} ref="stage" style={{marginLeft: - (this.props.as*100)+'vw'}}>
            <div className={s.stage}>
              <div className={s.container}>
                <div className={s.centerDiv}>
                  <img src="/diloo-big.png" className={s.logo} style={{marginTop: (this.state.height/2) - 200}} />
                </div>
              </div>
            </div>

        </div>
        <div className={started}>
        <Element name="init" ></Element>
        <Element name="inicio" >
          <div className={s.element}>
          <div className={s.black_ground}>
            <h2 className={cx(s.big,s.white,s.center_is)}>Calidad y eficiencia en soluciones de comunicación con el cliente</h2>
            </div>
          </div>
        </Element>
        <Element name="servicios" style={{backgroundImage: 'url(/fondo-servicios.jpg)',backgroundSize:'cover'}} >
          <div className={s.element}>
            <div className={cx(s.fullblack, s.right,s.sub,s.white,s.thin)} style={{textAlign:'right'}}>
              <div style={{paddingTop:'240px'}}>
                <h4 className={s.thin}>Servicio de venta on-line y <b>Comunicación Masiva</b><br /> personalizada para empresas</h4>
              </div>
            </div>
            <div  className={cx(s.grid)} >
              <Link to="/servicios/sms" onClick={this.callSms.bind(this)} className={cx(s.grid_child,s.cielo,{on_cielo: this.state.mouse_on && this.state.on_servicio == 1})} onMouseOver={this.onServicio.bind(this,1)} onMouseOut={this.offServicio.bind(this)}>
                <div className={s.full_center}>
                  <img src="/sms.png" />
                  <h4 className={cx(s.white,s.sub)}>SOLUCIONES SMS </h4>
                </div>
              </Link>
              <Link to="/servicios/voz" className={cx(s.grid_child,{velvet: this.state.mouse_on && this.state.on_servicio == 2})} onMouseOver={this.onServicio.bind(this,2)} onMouseOut={this.offServicio.bind(this)}>
                <div className={s.full_center}>
                  <img src="/voz.png" />
                  <h4 className={cx(s.white,s.sub)}>MENSAJES DE VOZ </h4>
                </div>
              </Link>
              <Link to="/servicios/sales" className={cx(s.grid_child,{blood: this.state.mouse_on && this.state.on_servicio == 3})} onMouseOver={this.onServicio.bind(this,3)} onMouseOut={this.offServicio.bind(this)}>
                <div className={s.full_center}>
                  <img src="/sales.png" />
                  <h4 className={cx(s.white,s.sub)}>CLICK TO SALES </h4>
                </div>
              </Link>
              <Link to="/servicios/mailing" className={cx(s.grid_child,{green: this.state.mouse_on && this.state.on_servicio == 4})} onMouseOver={this.onServicio.bind(this,4)} onMouseOut={this.offServicio.bind(this)}>
                <div className={s.full_center}>
                  <img src="/mailing.png" />
                  <h4 className={cx(s.white,s.sub)}>MAILING </h4>
                </div>
              </Link>
            </div>
          </div>
        </Element>

        <Element name="contacto"  >
          <div className={s.element}>
          <div className={cx(s.fullwhite,s.center)} >
            <div style={{paddingTop:'40px'}}>
              <img src="/blue-diloo.png" />

            </div>
          </div>

          </div>
        </Element>

        </div>

        {function(){

          if(this.props.servicio == 'sms'){
            return (
              <div className={s.servicio_show}>
                <div className={cx(s.fullwhite,s.center)} style={{backgroundImage : 'url('+require('./sms-fondo.jpg') + ')'}}>
                  <img src="/sms.png" className={s.icon_position} /> <h5 className={cx("on_cielo",s.servicio_title)}>SOLUCIONES SMS</h5>
                </div>
                <div className={s.grid}>
                  <Link to="/servicios">Cerrar</Link>
                </div>
              </div>
            )
          }
          if(this.props.servicio == 'voz'){
            return (
              <div className={s.servicio_show}>
                <div className={cx(s.fullwhite,s.center)} style={{backgroundImage : 'url('+require('./voz-fondo.jpg') + ')'}}>
                  <img src="/voz.png" className={s.icon_position} /> <h5 className={cx("velvet",s.servicio_title)}>MENSAJES DE VOZ</h5>
                </div>
              </div>
            )
          }
          if(this.props.servicio == 'sales'){
            return (
              <div className={s.servicio_show}>
                <div className={cx(s.fullwhite,s.center)} style={{backgroundImage : 'url('+require('./sales-fondo.jpg') + ')'}}>
                  <img src="/sales.png" className={s.icon_position} /> <h5 className={cx("blood",s.servicio_title)}>CLICK TO SALES</h5>
                </div>
              </div>
            )
          }
          if(this.props.servicio == 'mailing'){
            return (
              <div className={s.servicio_show}>
                <div className={cx(s.fullwhite,s.center)} style={{backgroundImage : 'url('+require('./mailing-fondo.jpg') + ')'}}>
                  <img src="/mailing.png" className={s.icon_position} /> <h5 className={cx("green",s.servicio_title)}>MAILING</h5>
                </div>
                <div className={s.grid}>
                  <div className={cx(s.header_grid, "dark-green")}>
                    <Link to="/servicios" className={s.close}>X</Link>
                    <div className={cx(s.two_grid,s.center)}>
                      <img src={require('./guru.png')}  />
                    </div>
                    <div className={cx(s.six,s.white)}>
                      Es una plataforma de Email Marketing 100% web para administrar listas, contactos y segmentos, enviar campañas de email y obtener reportes detalladosde su resultado.
                    </div>
                  </div>
                </div>
              </div>
            )
          }
        }.call(this)}






      </div>
    );
  }

}

export default InitPage;



